"use client"

import { useMemo } from "react"
// Import UI components
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
// Import data context
import { useWasteData } from "@/context/waste-data-context"

// Import Recharts components (still necessary for core chart structure)
import { Bar, BarChart, XAxis, YAxis, CartesianGrid } from "recharts" 

// Import shadcn chart wrappers and config
import {
  ChartConfig,
  ChartContainer,
  ChartTooltip,
  ChartTooltipContent,
} from "@/components/ui/chart" 

// 1. Define the Chart Configuration
// This replaces all the manual styling and formatting logic currently on XAxis and Tooltip.
const chartConfig = {
  tons: {
    label: "Tonnage Collected",
    // Match the original chart-2 color used in the fill prop
    color: "hsl(var(--chart-2))", 
  },
} satisfies ChartConfig

interface ChartDataPoint {
  lga: string;
  tons: number;
}

export function LgaBarChart() {
  const { submissions } = useWasteData()
  
  // 2. Data Calculation Logic (Unchanged from original file)
  const chartData: ChartDataPoint[] = useMemo(() => {
    const today = new Date().toISOString().split("T")[0]
    const lgaTotals: { [key: string]: number } = {}
    
    submissions
      .filter(s => s.date === today)
      .forEach(submission => {
        lgaTotals[submission.lga] = (lgaTotals[submission.lga] || 0) + submission.tons
      })
    
    return Object.entries(lgaTotals)
      .map(([lga, tons]) => ({ lga, tons: Math.round(tons * 10) / 10 }))
      .sort((a, b) => b.tons - a.tons)
      .slice(0, 10)
  }, [submissions])

  return (
    <Card className="border-0 shadow-sm">
      <CardHeader className="pb-2">
        <CardTitle className="font-serif text-lg text-primary">
          {"Today's Top 10 LGAs by Tonnage"}
        </CardTitle>
      </CardHeader>
      <CardContent>
        {/* 3. Implement the shadcn/ui Chart Component */}
        {/* ChartContainer replaces ResponsiveContainer and sets the config */}
        <ChartContainer config={chartConfig} className="min-h-[300px] w-full">
          <BarChart 
            accessibilityLayer
            data={chartData} 
            layout="vertical"
            // Adjust margin for vertical layout to give YAxis (LGA names) space
            margin={{ top: 5, right: 20, left: 10, bottom: 5 }} 
          >
            {/* CartesianGrid uses shadcn styling via CSS vars */}
            <CartesianGrid 
                strokeDasharray="3 3" 
                vertical={false} 
            />
            
            {/* XAxis (Tonnage Value) - Styling is now handled by ChartContainer */}
            <XAxis 
              type="number"
              // Add a simple formatter to replace the complex tickFormatter
              tickFormatter={(value) => `${value}t`} 
              tickLine={false}
              axisLine={false}
            />
            
            {/* YAxis (LGA Names - Category) - Styling is now handled by ChartContainer */}
            <YAxis 
              type="category"
              dataKey="lga"
              tickLine={false}
              axisLine={false}
              className="text-xs"
              width={90} // Ensure enough width for LGA names
            />
            
            {/* Tooltip uses shadcn wrapper for unified styling */}
            <ChartTooltip 
              content={
                <ChartTooltipContent 
                    nameKey="lga"
                    formatter={(value: number) => [`${value.toFixed(1)} tons`, "Collected"]}
                    // The background and border styling is now automatic
                />
              } 
            />
            
            {/* Bar Component - Fill color references ChartConfig */}
            <Bar 
              dataKey="tons" 
              fill="var(--color-tons)" // Reference the color from chartConfig
              radius={[0, 4, 4, 0]}
            />
          </BarChart>
        </ChartContainer>
      </CardContent>
    </Card>
  )
}