"use client"

import { useMemo } from "react"
// Import UI components: Card, etc.
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card" 
// Import data context
import { useWasteData, LGAS } from "@/context/waste-data-context" 
import { MapPin } from "lucide-react"

// Import Recharts components and shadcn chart wrappers
import { Bar, BarChart, XAxis, YAxis, CartesianGrid } from "recharts"
import {
  ChartConfig,
  ChartContainer,
  ChartTooltip,
  ChartTooltipContent,
} from "@/components/ui/chart" 

interface LgaStats {
  lga: string
  totalTons: number
  todayTons: number
  submissions: number
  trend: "up" | "down" | "stable"
  percentOfMax: number
}

// 1. Define the Chart Configuration
const chartConfig = {
  totalTons: {
    label: "Total Tonnage (tons)",
    // Using a primary color variable from your shadcn setup
    color: "hsl(var(--primary))", 
  },
  lga: {
    label: "Local Government Area",
  }
} satisfies ChartConfig

// Renamed the export to reflect the chart component
export function LgaTonnageChart() {
  const { submissions } = useWasteData()

  // 2. Data Calculation Logic (Unchanged from original file)
  const lgaStats = useMemo(() => {
    const today = new Date().toISOString().split("T")[0]
    const yesterday = new Date(Date.now() - 86400000).toISOString().split("T")[0]

    const stats: LgaStats[] = LGAS.map((lga) => {
      const lgaSubmissions = submissions.filter((s) => s.lga === lga)
      const totalTons = lgaSubmissions.reduce((acc, s) => acc + s.tons, 0)
      const todayTons = lgaSubmissions.filter((s) => s.date === today).reduce((acc, s) => acc + s.tons, 0)
      const yesterdayTons = lgaSubmissions.filter((s) => s.date === yesterday).reduce((acc, s) => acc + s.tons, 0)

      let trend: "up" | "down" | "stable" = "stable"
      if (todayTons > yesterdayTons * 1.1) trend = "up"
      else if (todayTons < yesterdayTons * 0.9) trend = "down"

      return {
        lga,
        totalTons: Math.round(totalTons * 10) / 10,
        todayTons: Math.round(todayTons * 10) / 10,
        submissions: lgaSubmissions.length,
        trend,
        percentOfMax: 0,
      }
    })

    const maxTons = Math.max(...stats.map((s) => s.totalTons), 1)
    stats.forEach((s) => {
      s.percentOfMax = (s.totalTons / maxTons) * 100
    })

    // Sort descending and limit to top 10 for chart readability
    return stats.sort((a, b) => b.totalTons - a.totalTons).slice(0, 10) 
  }, [submissions])

  // Prepare data for the Bar Chart
  const chartData = lgaStats.map(stat => ({
    lga: stat.lga,
    totalTons: stat.totalTons,
  }))

  return (
    <Card className="border border-border shadow-sm">
      <CardHeader className="pb-3">
        <div className="flex items-center gap-2">
          <MapPin className="w-5 h-5 text-accent" />
          <CardTitle className="font-serif text-lg text-primary">Top 10 LGA Waste Tonnage</CardTitle>
        </div>
        <CardDescription>Visual summary of total tonnage collected by the leading Local Government Areas.</CardDescription>
      </CardHeader>
      <CardContent>
        {/* 3. shadcn/ui Chart Implementation */}
        <ChartContainer config={chartConfig} className="min-h-[400px] w-full">
          <BarChart 
            accessibilityLayer 
            data={chartData} 
            layout="vertical" // Use vertical layout for ranking categories
            margin={{ left: 20, right: 30, bottom: 5 }}
          >
            <CartesianGrid horizontal={false} strokeDasharray="3 3" />
            
            {/* YAxis for LGA names (categories) */}
            <YAxis
              type="category"
              dataKey="lga"
              tickLine={false}
              axisLine={false}
              className="text-xs"
              width={100} // Give space for longer LGA names
            />
            
            {/* XAxis for Tonnage values (numbers) */}
            <XAxis 
              type="number" 
              dataKey="totalTons" 
              tickLine={false}
              axisLine={false}
              tickMargin={10}
              label={{ value: 'Tonnage (tons)', position: 'bottom', offset: 0, className: 'fill-muted-foreground text-sm' }}
            />
            
            {/* Tooltip for interaction */}
            <ChartTooltip content={<ChartTooltipContent nameKey="lga" hideIndicator />} />
            
            {/* The Bar itself */}
            <Bar 
              dataKey="totalTons" 
              fill="var(--color-totalTons)" 
              radius={4} 
              className="fill-primary"
            />
          </BarChart>
        </ChartContainer>
      </CardContent>
    </Card>
  )
}